import React from 'react'

export default function CardImg(props) {

  const { img, id, setCurrImg, setdisplay } = props

  const handleShowImg = (image) => {
    setdisplay(prev => !prev);
    setCurrImg(image)
  }

  return (
    <div className="col-md-4  rounded-4 " >
      <div className="cardimg position-relative rounded-4 overflow-hidden" onClick={() => handleShowImg(img)} >
        <img src={img} alt="" className=' rounded-4 w-100' id={id} />
        <div className="zoom p-0">
          <i className="fa-solid fa-magnifying-glass-plus fa-8x text-white" ></i>
        </div>
      </div>
    </div>

  )
}
