import React from "react";

export default function Modal(props) {
  const { img, setdisplay } = props;
  console.log(img);
  return (
    <div
      className=" position-fixed top-0 bottom-0 w-100 d-flex justify-content-center align-items-center bg-info bg-opacity-25 z-3"
      onClick={() => setdisplay((prev) => !prev)}
    >
      <img src={img} alt="" width="50%" />
    </div>
  );
}
