import { TextField } from '@mui/material'
import { Button } from '@mui/material'
import React from 'react'

export default function Contact() {
    return (
        <div className="contact container w-50 my-5">
            <div className="row   text-center">
                <h1 className='fw-bold'>CONATCT SECTION</h1>
                <div className="icon text-center d-flex justify-content-center p-3">
                    <div className='lineB'></div>
                    <i className="fa-solid fa-star "></i>
                    <div className='lineB'></div>
                </div>
                <form className='col-md-12 d-flex flex-column gap-5 justify-content-center'>
                    <TextField fullWidth label="UserName" id="fullWidth" variant="standard" />
                    <TextField fullWidth label="UserAge" id="fullWidth" variant="standard" />
                    <TextField fullWidth label="UserEmail" id="fullWidth" variant="standard" />
                    <TextField fullWidth label="UserPassword" id="fullWidth" variant="standard" />
                </form>
            </div>
            <Button variant="contained" color="success" className=' my-5'>Send Message</Button>
        </div>
    )
}
