import React, { useState } from "react";
import x from "../../assets/images/poert1.png";
import y from "../../assets/images/port2.png";
import z from "../../assets/images/port3.png";
import CardImg from "./CardImg";
import Modal from "./Modal";
export default function Portofolio() {
  const arr = [x, y, z, x, y, z];

  const [display, setdisplay] = useState(false);
  const [currImgSrc, setCurrImg] = useState(null);

  return (
    <div className="Portoflio my-5">
      <div className="container text-center  ">
        <h1 className="fw-bold ">PORTFOLIO COMPONENT</h1>
        <div className="icon text-center d-flex justify-content-center p-3">
          <div className="lineB"></div>
          <i className="fa-solid fa-star "></i>
          <div className="lineB"></div>
        </div>

        <div className="row g-5">
          {arr.map((ele, ind) => (
            <CardImg
              img={ele}
              id={ind}
              setCurrImg={setCurrImg}
              setdisplay={setdisplay}
            ></CardImg>
          ))}
        </div>
      </div>
      {display ? <Modal img={currImgSrc} setdisplay={setdisplay}/> : null}
    </div>
  );
}
